import axios from "axios"

export const fetchAllItems = () => {
  return axios
    .get("/items")
    .then(result => {
      console.log(result)
      return result.data
    })
    .catch(error => {
      console.log(error)
      return []
    })
}
export const postNewItem = item => {
  return axios
    .post("/items", item)
    .then(result => {
      console.log(result)
      return result.data
    })
    .catch(error => {
      console.log(error)
      return null
    })
}
